.first{
    width: 50%;
    height: 100%;
    padding: 5% 11% 0% 11%;
}
.heading{
    font-weight: 700;
    font-size: 36px;
    color: #282828;
}
.subheading{
    font-weight: 500;
    font-size: 20px;
    line-height: 160%;
}
.caption{
    font-weight: 800;
    font-size: 20px;
    line-height: 160%;
}
.e-form{
    display: flex;
}
.form-input{
    background: #FFFFFF;
    border: 4px solid #000000;
    border-radius: 8px;
    width: 58%;
    height: 64px;
    font-size: 20px;
    padding-left: 20px;
}
.form-button{
    background: #282828;
    border-radius: 8px;
    width: 38%;
    margin-left: 20px;
    color: white;
    font-size: 20px;
    font-weight: 700;
}
.form-button:hover{
    background: #282828b7;
}
.form-meta{
    color: white;
    font-size: 16px;
    line-height: 160%;
    font-weight: 500;
    padding-left: 30px;
    margin-top: 15px;
}

